.home .ant-layout-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.home .ant-layout-header .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}
.home .ant-layout-header .right-block {
  display: flex;
  align-items: center;
  margin-right: 2em;
}
.home .ant-layout-sider {
  box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
}
.home .logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}
